___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --background-body-color: #fcbf49;
  --font-color: #003049ff;
  --background-main-color: #fffdfa;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Sen', sans-serif;
}

body {
  background: var(--background-body-color);
  font-size: 1.5em;
  line-height: 1.2em;
  color: var(--font-color);
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: var(--background-main-color);
  padding: 10px;
  border-radius: 10px;
  text-align: center;
}

.container p {
  margin: 10px 0;
}

.qntCaracteres {
  width: 50%;
}

button {
  cursor: pointer;
  font-size: 1em;
  border: none;
  height: 35px;
  width: 200px;
  border-radius: 5px;
}

button {
  margin: 10px 10px;
  background: #db3539;
  color: var(--background-main-color);
}

.btnGerar {
  background: #3592d4;
}

.caracteres{
  display: flex;
  justify-content:center ;
}

.caracteres span{
  margin: 0 5px;
}

button:hover{
  transform: scale(1.05);
  filter: brightness(90%);
}

.botaoApertado{
  background: #109010 ;
